<template>
  <v-content>
    <v-container>
      <v-row justify="center"
            content="center">
        <v-col cols="12" md="6">
          <v-card class="pa-3">
            <v-row 
            justify="center"
            content="center">
              <v-col cols="12" sm="12" md="12">
                <v-text-field
            label="What To Do?"
            variant="outlined"
            placeholder="Write Your Work Title"
            v-model="item.title"
              ></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field
            label="What To Do?"
            variant="outlined"
            placeholder="Write Your Work Description"
            v-model="item.body"
              ></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-btn
              flat
              color="secondary"
              @click="saveToDo()"
              block>
      Save ToDo
    </v-btn>
              </v-col>
            </v-row>
            <br>
            <hr>
            <br>
            <v-subheader>Todo List</v-subheader>
            <v-row  justify="center"
            content="center">
          <v-list class="my-6">
            <v-list-item-group>
<div v-for="(item,index) in items" :key="index">

  <v-list-item value="notifications">
    <template v-slot:prepend="{ active }">
      
      <v-list-item-action start>
        <v-checkbox-btn v-model="item.active"></v-checkbox-btn>
      </v-list-item-action>
    </template>
    <v-list-item-title>{{item.title}}</v-list-item-title>
    
    <v-list-item-subtitle>
      {{item.body}}
    </v-list-item-subtitle>
  </v-list-item>
</div>
            </v-list-item-group>
      </v-list>
      <v-col cols="12" sm="12" md="12">
                <v-btn
              flat
              color="error"
              @click="deleteToDo()"
              block>
      Delete ToDo
    </v-btn>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-content>
</template>

<script>


// Components

export default{
  name: 'HomeView',
  data(){
    return{
      item:{
        title:'',
        body:'',
        active:false
      },
      items:[]
    }
  },
  watch:{
items:{
  handler(){
    localStorage.getItem('todos',JSON.stringify(this.items))
  }
}
  },
  methods:{
    saveToDo(){
      let emptyTodo={
        title:'',
        body:'',
        active:false
      }
      if(this.item.title.length>0 && this.item.body.length>0){

        this.items.push(this.item)
      }
      else{
        alert('please fill all bellow')
      }
     this.item=emptyTodo
      //saved item
    },
    deleteToDo(){
      this.items.forEach((todo,index)=>{
        if(todo.active===true){
          this.items.splice(index)
        }
      })
    }
  },
  mounted(){
    if(localStorage.getItem('todos')){
this.items=JSON.parse(localStorage.getItem('todos'))
    }
  }

 
};
</script>
